import { mxResources } from "@edp-pmf/mxgraph-ts";
import { Toolbox } from "./ToolBox";
import { ToolboxGroup } from "./ToolBoxGroup";
import { ToolboxItem } from "./ToolBoxItem";

/**
 * 默认工具箱
 */
export class DefaultToolbox extends Toolbox {
    constructor() {
        super();
        this.init();
    }

    /**
     * 初始化
     */
    init() {
        const g1 = new ToolboxGroup();
        g1.name = mxResources.get("Basic Element") || "基本元素";

        g1.items.push(
            new ToolboxItem("bpmn.StartEvent"),
            new ToolboxItem("bpmn.ExclusiveGateway"),
            new ToolboxItem("bpmn.InclusiveGateway"),
            new ToolboxItem("bpmn.ParallelGateway"),
            new ToolboxItem("bpmn.EndEvent"),
        );
        this.groups.push(g1);
    }
}